<template>
  <div id="sidebar" @mouseenter="big = true" @mouseleave="big = false">
    <game-button
      v-for="button in games"
      :key="button.title"
      :button="button"
      :full-button="big"
      @click.native="selectOption(button)"
    />
  </div>
</template>
<script>
import gameButton from './game-button.vue'
import Game from 'store/models/game'
export default {
  name: 'Sidebar',
  components: { gameButton },
  data () {
    return {
      big: false
    }
  },
  computed: {
    games () {
      return Game.all()
    }
  },
  methods: {
    selectOption (button) {
      this.$router.push({
        path: `/game/${button.tag}`
      })
    }
  }
}
</script>
<style lang="stylus" scoped>
.title
  margin-left 7.5px
  margin-top 10px
  margin-bottom 10px
#sidebar
  border-right 1.25px solid darken(white, 70%)
  height calc(100vh - 81.25px)
  display inline-block
  background-color $ui-background
  backdrop-filter blur(7.5px)
  width 50px
  transition width 0.25s ease-in-out
  &:hover
    width 220px
</style>
