<template>
  <div class="appbar">
    <div class="content">
      <div class="title">
        Plutonium
      </div>
      <div class="user">
        <div
          class="avatar"
          @click="openPeek"
        >
          <div
            :style="avatarStyle"
            class="avatarImage clickable round-highlight"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'AppBar',
  computed: {
    ...mapGetters({
      user: 'authentication/user'
    }),
    avatarStyle () {
      if (this.user.profilePic) {
        return `background-image: url('${this.user.profilePic}');`
      }
      return ''
    }
  },
  methods: {
    openPeek () {
      this.$store.dispatch('ui/setPeek', 'profile')
    }
  }
}
</script>
<style lang="stylus" scoped>
.appbar {
  width: 100%;
  min-height: 80px;
  background-color: $ui-background;
  border-bottom: 1.25px solid darken(white, 70%);

  .content {
    padding: 16px;
  }
}

.title {
  display: inline-block;
  text-transform: uppercase;
  font-family: politica, sans-serif;
  font-weight: 700;
  font-size: 38px;
  color: white;

  &:hover {
    filter: drop-shadow(0 0 8px alpha(#fff, 0.75));
    transition: filter 0.5s ease-in-out;
  }
}

// .user {
// display: inline-block;
// float: right;

// .avatar {
// display: inline;
// cursor: pointer;

// img {
// border-radius: 100%;
// width: 45px;
// border: 1px solid darken(white, 70%);
// padding: 0;
// margin: 0;
// }
// }
// }
.user {
  position: absolute;
  right: 32px;
  top: 16px;

  .avatar {
    display: inline;
    cursor: pointer;

    .avatarImage {
      background-image: url('~assets/avatar-default.jpg');
      background-size: cover;
      border-radius: 100%;
      width: 48px;
      height: 48px;
    }
  }
}
</style>
