<template>
  <div
    id="app"
    :class="backgroundClass"
  >
    <app-bar />
    <div class="row one">
      <sidebar />
      <div class="main">
        <router-view />
      </div>
    </div>
  </div>
</template>
<script>
import sidebar from '@/sidebar'
import appBar from '@/appbar'

export default {
  components: { sidebar, appBar },
  computed: {
    backgroundClass () {
      const path = this.$route.path
      if (path.includes('iw5mp')) return 'iw5mp'
      if (path.includes('t6mp')) return 't6mp'
      if (path.includes('t6zm')) return 't6zm'
      if (path.includes('t5mp')) return 't5mp'
      if (path.includes('t5sp')) return 't5sp'
      if (path.includes('t4mp')) return 't4mp'
      if (path.includes('t4sp')) return 't4sp'
      return 'default'
    }
  }
}
</script>
<style lang="stylus" scoped>
#app {
  height: 100vh;
  width: 100vw;

  &::before {
    content: '';
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.65;
    transition: background-image 0.4s ease-in-out;
    z-index: -1;
  }
}

.default::before {
  background-image: url('~assets/iw5.jpg');
}

.iw5mp::before {
  background-image: url('~assets/iw5.jpg');
}

.t6mp::before {
  background-image: url('~assets/t6mp.jpg');
}

.t6zm::before {
  background-image: url('~assets/t6zm.jpg');
}

.t5mp::before {
  background-image: url('~assets/t5mp.jpg');
}

.t5sp::before {
  background-image: url('~assets/t5sp.jpg');
}

.t4mp::before {
  background-image: url('~assets/t4mp.jpg');
}

.t4sp::before {
  background-image: url('~assets/t4sp.jpg');
}

.row {
  display: grid;
  grid-template-columns: min-content auto min-content;
}

.main {
  margin: 16px;
}
</style>
