<template>
  <div ref="button" class="button" :class="{ selected }">
    <div class="row">
      <div>
        <img v-if="button.icon" :src="button.icon" width="35px">
      </div>
      <div v-if="fullButton" class="buttonName">
        {{ button.title }}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'MenuButton',
  props: {
    button: {
      type: Object,
      required: false
    },
    fullButton: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    selected () {
      const route = this.$route
      return route.name === 'games' && route.path.includes(this.button.tag)
    }
  }
}
</script>
<style lang="stylus" scoped>
.button
  min-height 50px
  width 100%
  cursor pointer
  font-size 20px
  color $primary-4
  .buttonName
    padding-top 10px
    text-align left
    width 320px
  img
    margin-top 7.5px
    margin-left 7.5px
  &:hover, &.selected
    background-color alpha(white, 10%)
    color white

.actions
  text-align right

.action
  display inline-block
  background-color $primary-4
  padding 4px 6px 4px 6px
  margin 4px
  text-transform uppercase
  font-size 16px
  &.config
    background-color $primary-2
  &.play
    background-color $positive

.row
  display grid
  grid-template-columns 50px auto
</style>
